<template>


  <div class="app2">
    <!-- <div class="back" @click="onbacck">hssh</div> -->
    <div class="top">
          
    </div>
    <div class="home">
      <div class="icon">
        <div><van-icon class="icon_certificate" name="certificate" size="80px" color="#6666" /></div>
      </div>
      <p class="icon_p">订单支付成功</p>
      <div class="home_order">
        <div>
          <span>订单编号</span>
          <span>{{$route.query.Ordernumber}}</span>
        </div>
        <div>
          <span>下单时间</span>
          <span>{{nowtime}}</span>
        </div>
        <div>
          <span>支付方式</span>
          <span>余额支付</span>
        </div>
        <div>
          <span>支付金额</span>
          <span>￥：{{parseInt($route.query.price)>0?$route.query.price+'.00':$route.query.price}}</span>
        </div>
      </div>
      <div class="back"><van-button type="default" @click='Jump'>查看订单</van-button></div>
      <div class="back">
        <van-button type="default" @click="back" >回到首页</van-button>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: "paymentlist",
  data() {
    return {
    payment:[],
   
    };
  },
    created(){
    //结算
      this.$axios.get(`http://47.115.51.185/api/order/detail/${this.$route.query.Ordernumber}`).then(d=>{
        console.log(d);
        console.log(11);
        this.payment=d.data.data;
        console.log("111111111111111111111")
        console.log('支付数据',this.payment)
          console.log('支付列表',d);
          
            // console.log(this.valid);
        this.Totaiprice()
         
        
    });
    },
  methods: {
    //   onbacck(){
    //         this.$router.go(-2);
    //     },
        // 计算总价
        Totaiprice(){
            console.log(this.valid);
           this.valid.forEach((item)=>{
               console.log(item);
            //    price+=item.cart_num*item.item.productInfo.attrInfo.price
            this.price += item.cart_num * item.productInfo.price
           })
           console.log('总价格输出===>',this.price);
       
        },
      
    back  () {
      this.$router.push("/");
    },
    Jump(){
       this.$router.push({
         path:'/orderdetails',
          //传值
          query:{
            price: this.payment.cartInfo[0].productInfo.price,
            aaa: this.payment.order_id,
            
          }
       });
       
    },
   
  },
  computed: {
    nowtime: function () {
        // 下单时间
      var date = new Date();
      //获取当前的年
      var year = date.getFullYear();
      //获取当前的月
      var month = date.getMonth() + 1;
      //日
      var day = date.getDate();
      //时
      var hh = date.getHours();
      //分
      var mm = date.getMinutes();
      mm>10?mm:'0'+mm
      //秒
      var ss = date.getSeconds();
      ss>10?ss:'0'+ss
      var rq =
        year + "-" + month + "-" + day + '    ' + hh + ":" + mm + ":" + ss;
        return rq;
      
    },
  },

};
</script>
